@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@100&display=swap);
/* colors */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 0.7em 0;
  background: none;
  display: flex;
  justify-content: center;
  z-index: 44; }
  .navbar__items {
    display: flex;
    align-items: center;
    width: 100%;
    height: 50px;
    z-index: 33; }
    .navbar__items__list {
      list-style: none;
      display: flex;
      align-items: center;
      width: 100%; }
      .navbar__items__list a {
        text-decoration: none;
        text-transform: capitalize;
        font-size: 1.2em;
        font-weight: 600;
        font-family: 'Fira Sans', sans-serif;
        color: #000;
        padding: 8px 16px;
        cursor: pointer; }
        .navbar__items__list a:hover {
          color: #1292b4; }
  .navbar__menu {
    position: absolute;
    top: 0;
    left: 10px;
    display: none;
    cursor: pointer; }
    .navbar__menu > div {
      width: 35px;
      height: 5px;
      background-color: white;
      margin: 6px 0;
      transition: 0.4s; }
    .navbar__menu__change > :nth-child(1) {
      transform: rotate(-45deg) translate(-9px, 6px); }
    .navbar__menu__change > :nth-child(2) {
      opacity: 0; }
    .navbar__menu__change > :nth-child(3) {
      transform: rotate(45deg) translate(-8px, -8px); }

.sticky {
  background-color: #fff;
  opacity: 1;
  top: 0px; }

@media (max-width: 599px) {
  .navbar {
    font-size: 0.889rem; } }
  @media screen and (max-width: 599px) and (min-width: 20rem) {
    .navbar {
      font-size: calc(-0.21262vw + 0.93152rem); } }
  @media screen and (max-width: 599px) and (min-width: 85.375rem) {
    .navbar {
      font-size: 0.75rem; } }

@media (max-width: 599px) {
  .navbar__items {
    padding: 0; }
    .navbar__items__list a {
      padding: 0 8px; } }

